import React, { useContext } from 'react';
import { Link, useNavigate, NavLink } from 'react-router-dom';
import { AuthContext } from '../contexts/AuthContext';
import '../styles/NavigationBar.scss';

const NavigationBar = () => {
  const { isAuthenticated, logout } = useContext(AuthContext);
  const navigate = useNavigate();

  const handleLogout = () => {
    logout();
    navigate('/');
  };

  return (
    <div className="navbar">
      <div className="logo">
      <Link to="/" className="logo-link">
          <span className="logo-text">Livestock</span>
          <span className="logo-highlight">Rx</span>
        </Link>
      </div>

      <div className="menu" >
        <NavLink to="/conversation" className={({ isActive }) => isActive ? "menu-item active" : "menu-item"}> Health</NavLink>
        <NavLink to="/MedicationInfo" className={({ isActive }) => isActive ? "menu-item active" : "menu-item"}> </NavLink>
        <NavLink to="/calendar" className={({ isActive }) => isActive ? "menu-item active" : "menu-item"}></NavLink>
        <NavLink to="/vaccin" className={({ isActive }) => isActive ? "menu-item active" : "menu-item"}> </NavLink>
        <NavLink to="/animals" className={({ isActive }) => isActive ? "menu-item active" : "menu-item"}> </NavLink>
        <NavLink to="/subscribe" className={({ isActive }) => isActive ? "menu-item active" : "menu-item"}></NavLink>
      </div>

      <div className="auth">
        {isAuthenticated ? (
          <>
            <NavLink to="/mypage" className="auth-button mypage-button"></NavLink>
            <button onClick={handleLogout} className="auth-button logout-button"></button>
          </>
        ) : (
          <>
            <NavLink to="/login" className="auth-button login-button"></NavLink>
            <NavLink to="/signup" className="auth-button signup-button"></NavLink>
          </>
        )}
      </div>
    </div>
  );
};

export default NavigationBar;