import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import '../styles/AnimalList.scss';

const AnimalList = () => {
    const [animals, setAnimals] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [newAnimal, setNewAnimal] = useState({
        tag_number: '',
        species: 'cow',
        gender: 'male',
        birth_date: '',
        weight: '',
    });

    useEffect(() => {
        fetchAnimals();
    }, []);

    const fetchAnimals = async () => {
        try {
            const token = localStorage.getItem('token');
            const response = await axios.get('/api/my-animals', {
                headers: { Authorization: `Bearer ${token}` }
            });
            setAnimals(response.data);
            setLoading(false);
        } catch (err) {
            setError('    .');
            setLoading(false);
        }
    };

    const handleInputChange = (e) => {
        setNewAnimal({ ...newAnimal, [e.target.name]: e.target.value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            await axios.post('/api/animals', newAnimal, {
                headers: { Authorization: `Bearer ${localStorage.getItem('token')}` },
            });
            setNewAnimal({
                tag_number: '',
                species: 'cow',
                gender: 'male',
                birth_date: '',
                weight: '',
            });
            fetchAnimals();
        } catch (err) {
            console.error('Error adding animal:', err);
        }
    };

    if (loading) return <div> ...</div>;
    if (error) return <div>{error}</div>;

    return (
        <div className="animal-list">
            <h2> </h2>
            <table>
                <thead>
                    <tr>
                        <th> </th>
                        <th></th>
                        <th></th>
                        <th> </th>
                    </tr>
                </thead>
                <tbody>
                    {animals.map(animal => (
                        <tr key={animal.id}>
                            <td>{animal.tag_number}</td>
                            <td>{animal.species === 'cow' ? '' : ''}</td>
                            <td>{animal.gender === 'male' ? '' : ''}</td>
                            <td>
                                <Link to={`/animals/${animal.id}`}> </Link>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>

            <h3>  </h3>
            <form onSubmit={handleSubmit}>
                <input
                    type="text"
                    name="tag_number"
                    placeholder=" "
                    value={newAnimal.tag_number}
                    onChange={handleInputChange}
                    required
                />
                <select name="species" value={newAnimal.species} onChange={handleInputChange} required>
                    <option value="cow"></option>
                    <option value="pig"></option>
                </select>
                <select name="gender" value={newAnimal.gender} onChange={handleInputChange} required>
                    <option value="male"></option>
                    <option value="female"></option>
                </select>
                <input
                    type="date"
                    name="birth_date"
                    value={newAnimal.birth_date}
                    onChange={handleInputChange}
                    required
                />
                <input
                    type="number"
                    name="weight"
                    placeholder=" (kg)"
                    value={newAnimal.weight}
                    onChange={handleInputChange}
                    required
                />
                <button type="submit"></button>
            </form>
        </div>
    );
};

export default AnimalList;
