import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import '../styles/AnimalDetails.scss';

function AnimalDetails() {
    const [animal, setAnimal] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const { id } = useParams();

    useEffect(() => {
        fetchAnimalDetails();
    }, [id]);

    const fetchAnimalDetails = async () => {
        try {
            const token = localStorage.getItem('token');
            const response = await axios.get(`/api/animals/${id}`, {
                headers: { Authorization: `Bearer ${token}` }
            });
            setAnimal(response.data);
            setLoading(false);
        } catch (err) {
            console.error('Error fetching animal details:', err);
            setError('    .');
            setLoading(false);
        }
    };

    if (loading) return <div> ...</div>;
    if (error) return <div>{error}</div>;
    if (!animal) return <div>    .</div>;

    return (
        <div className="animal-details-container">
            <h2 className="animal-details-title"> </h2>
            <div className="animal-details-card">
                <div className="animal-info">
                    <h3> </h3>
                    <p><strong> :</strong> {animal.tag_number}</p>
                    <p><strong>:</strong> {animal.species === 'cow' ? '' : ''}</p>
                    <p><strong>:</strong> {animal.gender === 'male' ? '' : ''}</p>
                    <p><strong>:</strong> {animal.weight} kg</p>
                    <p><strong>:</strong> {new Date(animal.birth_date).toLocaleDateString()}</p>
                    {animal.slaughter_date && <p><strong>:</strong> {new Date(animal.slaughter_date).toLocaleDateString()}</p>}
                </div>
                <div className="animal-metadata">
                    <h3></h3>
                    <p><strong>  :</strong> {new Date(animal.created_at).toLocaleString()}</p>
                    <p><strong>  :</strong> {new Date(animal.updated_at).toLocaleString()}</p>
                    <p><strong>:</strong> {animal.memo}</p>
                </div>
            </div>

            
            {animal.vaccinations && animal.vaccinations.length > 0 && (
                <div className="animal-records">
                    <h3>  </h3>
                    <ul className="records-list">
                        {animal.vaccinations.map(vac => (
                            <li key={vac.id} className="record-item">
                                <span className="record-name">{vac.vaccine_name}</span>
                                <span className="record-date">{new Date(vac.date).toLocaleDateString()}</span>
                            </li>
                        ))}
                    </ul>
                </div>
            )}

            {/** 
            {animal.medicationRecords && animal.medicationRecords.length > 0 && (
                <div className="animal-records">
                    <h3>  </h3>
                    <ul className="records-list">
                        {animal.medicationRecords.map(rec => (
                            <li key={rec.id} className="record-item">
                                <span className="record-name">{rec.medication_name}</span>
                                <span className="record-date">{new Date(rec.date).toLocaleDateString()}</span>
                                <span className="record-dosage">: {rec.dosage}</span>
                            </li>
                        ))}
                    </ul>
                </div>
            )}
            */}
        </div>
    );
}

export default AnimalDetails;