import React from 'react';
import '../styles/Subscribe.scss';

const Subscribe = () => (
  <div>
    <div className="subscription-container">
      <div className="subscription-card">
        <h3></h3>
        <p>30  </p>
        <button onClick={() => subscribe('trial')}></button>
      </div>
      <div className="subscription-card">
        <h3>1</h3>
        <p> </p>
        <button onClick={() => subscribe('1month')}></button>
      </div>
      <div className="subscription-card">
        <h3>3</h3>
        <p> </p>
        <button onClick={() => subscribe('3months')}></button>
      </div>
      <div className="subscription-card">
        <h3>6</h3>
        <p> </p>
        <button onClick={() => subscribe('6months')}></button>
      </div>
    </div>
  </div>
);

const subscribe = (plan) => {
  alert(`${plan}  .`);
};

export default Subscribe;
