import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import '../styles/SignUp.scss';

const SignUp = () => {
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [role, setRole] = useState('farmer'); //  
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      await axios.post('/api/auth/signup', { username, password, email, role });
      setError(''); //   
      //localStorage.setItem('token', response.data.token);
      navigate('/login'); //   
    } 
    catch (err) {
      console.error('Error:', err);
      setError(err.response?.data?.message || err.message || 'Unknown error occurred');
    }
    finally {
      setLoading(false);
    }
  };

  return (
    <div className="container form-container">
      <h2 className="text-center"></h2>
      {error && <div className="alert alert-danger">{error}</div>}
      {loading ? (
        <div className="spinner-border text-primary" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      ) : (
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="username"></label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="password"></label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="email"></label>
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="name"></label>
          <select value={role} onChange={(e) => setRole(e.target.value)}>
            <option value="farmer"></option>
            <option value="vet"></option>
          </select>
        </div>

        {/** 
        <div className="form-group">
          <label htmlFor="birthdate"></label>
          <div className="d-flex">
            <input type="number" className="form-control mr-1" id="birthYear" name="birthYear" placeholder="" required />
            <input type="number" class="form-control mr-1" id="birthMonth" name="birthMonth" placeholder="" required />
            <input type="number" class="form-control" id="birthDay" name="birthDay" placeholder="" required />
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="residence"></label>
          <input type="text" className="form-control" id="residence" name="residence" required />
        </div>
        */}

        <button type="submit" className="btn btn-primary btn-block"></button>
      </form>
      )}
    </div>
  );
};

export default SignUp;
